<template>
  <div>正在验证2...</div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
    getParameter: function (name) {
        var query = window.location.search;
        var iLen = name.length;
        var iStart = query.indexOf(name);
        if (iStart == -1)
            return "";
        iStart += iLen + 1;
        var iEnd = query.indexOf("&", iStart);
        if (iEnd == -1)
            return query.substring(iStart);
        return query.substring(iStart, iEnd);
    },
    isWeiXin: function() {
        var ua = window.navigator.userAgent.toLowerCase();
        if (ua.match(/MicroMessenger/i) == 'micromessenger') {
            return true; // 是微信端
        } else {
            return false;
        }
    }
  },
  mounted () {
    var role_id = this.getParameter('role_id') || '179163154';
    //微信端验证
    if (this.isWeiXin()) {
      
      let url = 'https://exth5.dev.16163.com?role_id='
      let callbackUrl = 'https%3a%2f%2fexth5.dev.16163.com%3frole_id%3d';
      let redirect_uri = 'https%3a%2f%2fgame.16163.com%2fmhshow_h5%2fxxxx%3frole_id%3d1234';
      let code = this.getParameter('code');
      let appid = 'wx0376800bd1071c94';

      //微信回调含code调用免登陆接口，否则跳转微信验证获取code
      if (code) {
          document.write('已获取code...')
          localStorage.setItem('wxcode',code)
          localStorage.setItem('role_id',role_id)
          this.$router.push('/index')
          
      } else {
        document.write('跳转至微信验证...')
        window.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appid}&redirect_uri=${callbackUrl}${role_id}&response_type=code&scope=snsapi_base&state=mhshow_h5`;
      }
    } else {
      //大神端验证
      ds.ready().then(function() {
        if (ds.isGodlike) {
          alert('已进入大神客户端')
          // ds.checkLogined().then(function(res) {
          //   alert('checkLoginedcheckLoginedcheckLogined')
          //   alert(JSON.stringify(res));
          //   if(res && res.isLogined) {
          //     ds.getAccessToken({
          //       appId: '5d81e28d1da86cffea4b7829'
          //     }).then(function(res) {
          //       alert(JSON.stringify(res));
          //       if(res && res.code === '200') {
          //         ds.getBindGameRoleList({
          //           gameCode: '',
          //           minLevel: 0
          //         }).then(function(res) {
          //           alert(JSON.stringify(res));
          //           if(res && res.code === '200') {
          //             alert(res)
          //           }
          //         });
          //       }
          //     });
          //   }
          // });
          ds.checkLogined().then((res) => {
            alert('checkLogined', JSON.stringify(res))
            return res
          }).then(res => {
            if(res && res.isLogined) {
              ds.getAccessToken({
                appId: '5d81e28d1da86cffea4b7829'
              }).then((res) => {
                alert('getAccessToken', JSON.stringify(res));
                return res
              })
            }
          }).then(res => {
            if(res && res.code === 200) {
               ds.getBindGameRoleList({
                 gameCode: '',
                 minLevel: 0
               }).then((res) => {
                 alert('getBindGameRoleList', JSON.stringify(res));
               })    
            }
          })
          //外部浏览器验证
        } else {
          alert('已进入外部浏览器')
          var mobileShare = new MobileShare(options); // 初始化分享组件
          mobileShare.show(); 
          console.log(mobileShare)
        }
      });
    }



  }
}
</script>


